import { HamburgerIcon } from '@chakra-ui/icons';
import { Center, Flex, Heading, IconButton, Menu, MenuButton, MenuItem, MenuList, Square } from '@chakra-ui/react';
import React from 'react';
import { FaListOl, FaSlidersH } from 'react-icons/fa';
import { useHistory, useLocation, withRouter } from 'react-router-dom';

const Navigation: React.FC = () => {
  const history = useHistory();
  const location = useLocation();

  return (
    <Flex as="nav">
      <Center h="50px" w="50px">
        <Menu>
          <MenuButton as={IconButton} aria-label="Options" icon={<HamburgerIcon />} variant="outline" />

          <MenuList>
            <MenuItem
              icon={<FaListOl />}
              as="div"
              disabled={location.pathname === '/'}
              onClick={(event) => {
                event.stopPropagation();
                event.preventDefault();

                if (location.pathname === '/') {
                  return;
                }

                history.push({ pathname: '/' });
              }}
            >
              글자 수 세기
            </MenuItem>

            <MenuItem
              icon={<FaSlidersH />}
              as="div"
              disabled={location.pathname === '/layout'}
              onClick={(event) => {
                event.stopPropagation();
                event.preventDefault();

                if (location.pathname === '/layout') {
                  return;
                }

                history.push({ pathname: '/layout' });
              }}
            >
              문장 배열하기
            </MenuItem>
          </MenuList>
        </Menu>
      </Center>

      <Square style={{ marginRight: 35, justifyContent: 'center', flexGrow: 1 }}>
        <Heading as="h1"> 서예 도움: {location.pathname === '/' ? '글자 수 세기' : '문장 배열하기'} </Heading>
      </Square>
    </Flex>
  );
};

export default withRouter(Navigation);
